import React from 'react';
import {useEffect} from 'react';
import './osm.css';
import Axios from 'axios';

function OSM(props) {


  let url = "";

  useEffect(() => {
   Axios.get('https://osm-server.herokuapp.com/values/getAll/getOSmLink').then((response)=>{
      console.log(response.data)
      if( response.data.length > 0 && response.data[0].value1 ){
        url=response.data[0].value1;
        console.log(url)
      }
    });
  }, []);


    var OSMRedirect=()=>{
        window.open("https://onestopmusic.github.io", "_blank");
      }

    var OSMAppRedirect=()=>{
        window.open(url, "_blank");
    }


  return (
    <div className="osm">
      <div className="scrShts">
        <img className="scrsht" src={props.images[0]} alt="logo" />
        <img className="scrsht" src={props.images[1]} alt="logo" />
      </div>
      <div className="bottomPart">
        <div className="textOSM">
            <div className="Header">
                    {/* One.Stop.Music */}
                    <img className="banner" src={props.images[2]} alt="banner" />
            </div>
            <div className="techstack">
                    TECH STACK: Angular 6, Java Spring-Boot, PostgreSQL, Android Studio
            </div>
            <div className="desc">
                    Audio Player available as Android app and Web app. 
                    Plays audio of Youtube videos, runs in background, with multiple playlists and everything else. Order maintained through <b>Lexorank</b>.
            </div>
        </div>
        <div className="linkButtons">
            <div onClick={OSMAppRedirect} role="button" className="andrLink">
              <img className="linkicon" src={props.buttons[0]} alt="logo" />
              {/* <div className="linkText">APK</div> */}
            </div>
            <div onClick={OSMRedirect} role="button" className="webLink">
              <img className="linkicon" src={props.buttons[1]} alt="logo" />
              {/* <div className="linkText">Web</div> */}
            </div>
        </div>
      </div>
    </div>
  );
}

export default OSM;