
import React from 'react';
import Skills from './Skills/skills';
import About from'./About/about';
import Experience from './Experience/experience'
import OSM from './OSM/osm'
import GMA from './GetMyApp/gma';
// osm SCs and banner
import expSC from './Assets/osmEx.svg';
import smlSC from './Assets/osmSm.svg';
import osmBan from './Assets/osm.png';

// Buttonss
import andr from './Assets/apk.png';
import web from './Assets/web.png';


// GMA SCs and banner
import gma1 from './Assets/gma1.svg';
import gma2 from './Assets/gma2.svg';
import gma4 from './Assets/gma4.svg';
import gma5 from './Assets/gma5.svg';
import gma7 from './Assets/gma7.svg';
import gma8 from './Assets/gma8.svg';
import gmaBan from './Assets/h3.png';
  
var osmImages=[expSC,smlSC,osmBan];

var gmaImages=[gma1,gma2,gma4,gma5,gma7,gma8,gmaBan];

var buttons=[andr,web];

export default [
	{
		title: 'One Stop Music',
		component: <OSM images={osmImages} buttons={buttons} />,
		bgColor:"#005ba9",
		bgGrad:"linear-gradient(45deg,  #3a6073 ,#1e1028)",
	},
	{
		title: 'Get My App',
		component: <GMA images={gmaImages} buttons={buttons} />,
		bgColor:"#005ba9",
		bgGrad:"linear-gradient(45deg, #032d4c , #628781)",
	},
	{
		title: 'Skills',
		component: <Skills/>,
		bgColor:"#f4b93d",
		bgGrad:"linear-gradient(45deg, #ede574, #e1f5c4)",
	},
	{
		title: 'Experience',
		component: <Experience/>,
		bgColor:"#c392cc",
		bgGrad:"linear-gradient(45deg, #dae2f8, #d6a4a4)",
	},
	{
		title: 'Projects',
		component: <About/>,
		bgColor:"#eabad8",
		bgGrad:"linear-gradient(45deg, #d8fd80, #a8ffd6)",
	},
	
	
	
];